.header{
    display:flex;
    align-items: center;
    justify-content: space-between;
    background-color: #000031;
    position: sticky;
    top:0;
    z-index: 100;
    height: 60px;
    padding: 10px;
}
.header__left{
    color:gray;
    /* background-color: #fafafa; */
    border-radius: 20px;
    font-weight: 800;
    display:flex;
    align-items: center;
    border-left: 1px solid lightgray;
    color: gray;
    /* text-shadow:  5px 10px 20px rgba(0,0,0,0.5); */
    width:20vh;
    height: 6vh;
    text-shadow: 5px 10px 20px rgba(0,0,0,0.5);
}
.header__left > Divider{
    border-left: 1px solid lightgray;
    color: gray;
}
.header__input{
    background-color: white;
    display:flex;
    align-items: center;  
    margin: 0px;
    border-radius: 10px;
    width: 240px;
    box-shadow: 5px 5px 20px 5px  rgba(0,0,0,0.5);

}

.header__input >input {
    height: 30px;
    outline: none;
    margin:5px;
    border: none;
}

.header__inputButton{
    width: 50px !important;
    background-color: #fafafa;
    border-left: 1px solid lightgray;
    color: gray;
}


.header__right{
    color:white;
    display:flex;
    align-items: center;
}

.header__right >Button{
    color:white;
    margin: 10px;
    border-radius: 32px;
    box-shadow: 5px 5px 20px 5px  rgba(0,0,0,0.5);
}

.loupeIcon{
    color:gray;
    margin:10px;
}
