.body{
    background-color: #000031;
    height: 88vh;
    flex: 0.8;
    /* mask-composite:linear-gradient(to bottom, rgba(0,0,0,1),rgba(0,0,0,0)); */
}

.body__header{
  display: flex;  
  align-items: center;
  justify-content: space-between;
  margin: 10px;
  padding: 10px;
}
.body__headerImg{
    display: flex;
    border-radius: 20px;
    box-shadow: 5px 5px 20px 5px rgba(0,0,0,0.5);
    flex: 0.6;

}
.body__headerPara{
    color: white;
    flex: 0.4;
    padding: 5px;
    margin-left: 10px;
}

.bodyVideos__videos{
    display: flex;
    flex-wrap: wrap;
    color: white;
    margin: 10px;
    margin-left: 20px;
    border-radius: 10px;
}
.body__videos{
  display: flex;
  color:white;
  /* justify-content: space-between; */
  margin-right: 20px;
    
}
.body__videosOne>p{
    color:white;
    margin-left: 40px;
}

