.sidebarRow{
    display:flex;
    align-items: center;
    padding :10px 20px ;
}
.sidebarRow__icon{
    color: #e9e3e3;
    font-size: larger !important;
}
.sidebarRow__title{
    flex: 1;
    margin-left: 20px;
    font-size: 18px;;
    font-weight: 400;
    color:white;
}

.sidebarRow:hover{
    background-color: rgb(221, 118, 118);
    cursor:pointer;
}
.sidebarRow:hover >.sidebarRow__icon{
    color:red;
}
.sidebarRow:hover >.sidebarRow__title{
    font-weight: bold;
}