.sidebar{
    background-color: #000031;
    height:88vh;
    flex:0.2;
    position: sticky;
    left: 0;
    border-right: 0.1px solid gray;
}

.sidebar__body{
    border: 1px;
}
.sidebar__footer{
    display:flex;
    flex-direction: row;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 20%;
    margin-bottom: 10%;
    color:white;
}
.sidebar__footerOption{
    display:flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    padding: 10px;
    color:white;
    background-color: grey;
    box-shadow: 5px 5px 20px 5px rgba(0,0,0,0.5);
    height: 60px;
    width: 100px;
    border-radius: 20px;
}
.sidebar__footerOptionOne{
    font-size: 10px;

}
.sidebar__footerOptionTwo{
    font-size: 13px;
    font-weight: 800;

}